body{
    
    /*background-image: linear-gradient(180deg,#4eafa7, #8decad);*/
    font-family: 'Roboto', sans-serif;
    font-size: small;
}
header{
  color: whitesmoke;
  margin: auto;
  display: flex;
  flex-direction: row;
  gap: 2rem;
}
p{
  font-size: 34px;
}

a { 
  color:#ff6e54;
  text-decoration: none;
  text-transform: uppercase;
}
a:hover, a:active {
  color:#ffa600;
}

.title {
  padding:1em 1em 2em 2em;
  max-width: 500px;
}
.dasboardapp{
    width: 85%;
    margin: auto;
    display: flex;
    flex-direction: column;
}
.chart-wrapper {
    padding: 10px;
    display: flex;
  }

  .sub-wrapper {
      width: 100%;
      margin: 0 auto;
      display: flex;
      flex-direction: column;
      align-content: space-around;
      
    
  }
  
  .main.chart-wrapper {
    height: 250px;
  }
  
  .sub.chart-wrapper {
    height: 250px;
    width: 80%;
  
  }

  .doughnut{
    margin: 0 auto;
  }
  
  @media (min-width: 700px) {


   
    .sub-wrapper{
      flex-direction: row;
      justify-content: space-evenly;
    }
  }



